$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-setting-responsive-control-wrapper {
    .fb-setting-responsive-control {
        margin-bottom: 0;
    }
}

.fb-testimonial-image-control-wrapper {
    margin-bottom: 16px;
    .components-base-control__field {
        display: block;
    }
    .fb-testimonial-image-control {
        background-color: #ccc;
        background-position: 50%;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 6px;
        height: 150px;
        margin-bottom: 16px;
        padding: 2px;
        position: relative;
        width: 100%;
        .fb-testimonial-image-delete {
            margin: 0 8px !important;
            padding: 0 !important;
            position: absolute;
            right: 0;

            align-items: center;
            background: none;
            border: 0;
            border-radius: 2px;
            cursor: pointer;
            display: inline-flex;
            font-family: inherit;
            font-weight: 400;
            height: 36px;
            text-decoration: none;
            transition: box-shadow .1s linear;

            svg {
                fill: none;
                filter: drop-shadow(1px 1px 3px rgb(100, 100, 100));
            }
        }
        .fb-testimonial-image-replace-btn {
            -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
            background: hsla(0, 0%, 95%, .749);
            border-radius: 0 0 6px 6px;
            bottom: 0;
            display: flex;
            gap: 10px;
            justify-content: center;
            left: 0;
            position: absolute;
            transition: .3s;
            width: 100%;
            color: #000;
            font-size: 13px;

            align-items: center;
            border: 0;
            cursor: pointer;
            font-family: inherit;
            font-weight: 400;
            height: 36px;
            margin: 0;
            padding: 6px 12px;
            text-decoration: none;
            svg {
                fill: none;
            }
        }
    }
    .fb-testimonial-image-upload-btn {
        background-color: #f2f2f2;
        background-image: url(transparent-bg-demo.png);
        border: 1px solid #e9e8eb;
        border-radius: 6px;
        height: 130px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 8px;
        justify-content: center;
        align-items: center;
        padding: 0;
        cursor: pointer;
        font-weight: 400;
        margin: 0;
        text-decoration: none;
        transition: box-shadow .1s linear;
    }
}

.fb-testimonial-section {
    display: block;
    transition: ease-in-out 0.5s;

    &.fb-testimonial-section-left {
        text-align: left;
    }

    &.fb-testimonial-section-center {
        text-align: center;
    }
    &.fb-testimonial-section-right {
        text-align: right;
    }

    .fb-testimonial-wrapper {
        width: 100%;
    }

    .fb-testimonial-rating {
        .fb-testimonial-rating-icon {
            display: inline-block;
        }
        svg {
            transition: ease-in-out 0.5s;
        }
    }

    .fb-testimonial-author-container {
        display: flex;
        align-items: center;
        &.left {
           justify-content: flex-start;
        }
        &.center {
            justify-content: center;
        }
        &.right {
            justify-content: flex-end;
        }
        .fb-testimonial-author-inner-container {
            display: flex;
            align-items: center;
            .fb-testimonial-author-image-container {
                .fb-testimonial-image {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img {
                        max-width: 100%;
                    }
                }
            }
    
            .fb-testimonial-userinfo-container {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
        }
    }

    .fb-testimonial-quote-icon {
        display: inline-block;
        svg {
            transition: ease-in-out 0.5s;
        }
    }
}
